import App from "../App";
import React from "react";
import { Link, Route, BrowserRouter as Router } from "react-router-dom";
 
import Home from "./Home";
import UserPage from "./UserPage";
import FormSubmit from "./FormSubmit";

const Home1=({match})=>(<h1>{match.params.name}</h1>)
    


export default class Router1 extends React.Component {

    render() {
        return (<Router>
            <div id="menu">
                <li><Link to="/home1/liubo">Home1</Link></li>
                <li><Link to="/home/liubo">Home</Link></li>
                <li><Link to="/userPage">userPage</Link></li>
                <li><Link to="/app">App</Link></li>
                <li><Link to="/form">鲁志伟</Link></li>
            </div>

            <div id="page_content">
            
                <Route path="/home1/:name" component={Home1} />
                <Route path="/home/:name" component={Home} />
                <Route path="/userPage" component={UserPage} />
                <Route path="/app" component={App} />
                <Route path="/form" component={FormSubmit} />

            </div>
        </Router>)

    }
}